.nav {
	&-link-home,
	&-link-about {
		text-decoration: underline;
		color: black;
		margin-left: 2.5rem;
		font-size: 1.5rem;
		font-weight: 500;
		text-transform: capitalize;

		@include mobile-breakpoint {
			text-transform: uppercase;
			font-weight: 700;
			margin-left: 2rem;
			font-size: 0.8rem;
		}

		@include mobile-breakpoint-400 {
			font-size: 0.5rem;
			margin-left: 0.2rem;
			
		}
	}

}